.for-whom {
	position: relative;
	padding: 95px 0;
	@include clr;
	&__list {
		margin-top: 65px; }
	&__item {
		font-size: 20px;
		font-weight: 700;
		padding-left: 70px;
		position: relative;
		&:not(:first-child) {
			margin-top: 25px; }
		&:before {
			content: "";
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background-color: $orange;
			@include circle(40); } }
	&__img {
		position: absolute;
		right: 0;
		bottom: 40px;
		z-index: -1;
		@include md {
			position: static;
			margin-top: 20px;
			@include clr; }
		img {
			display: block;
			max-width: 100%;
			@include md {
				float: right; } } } }
