body {
	background: $white;
	line-height: 1.4;
	font-family: $museo;
	font-size: 16px;
	color: #363636; }


.btn {
	position: relative;
	overflow: hidden;
	font-weight: 700;
	text-transform: uppercase;
	text-decoration: none;
	font-family: $museo;
	font-size: 14px;
	text-align: center;
	line-height: 2.36;
	padding: 12px 39px;
	background-color: $orange;
	color: $white;
	display: inline-block;
	border-radius: 3px;
	transition: background-color .3s;
	&:hover {
		background-color: $blue; }
	@include xs {
		padding: 4px 15px; }
	&_wide {
		width: 100%;
		padding: 12px 10px; }
	&_big {
		padding: 12px 60px; }
	&_modal {
		padding: 12px 50px; }
	&_dark {
		background-color: $darkBlue; }
	&__text {
		position: relative;
		z-index: 1;
		display: block; } }

.title {
	position: relative;
	font-size: 40px;
	font-weight: 900;
	line-height: 1;
	padding-bottom: 22px;
	color: $black;
	@include sm {
		font-size: 24px;
		padding-bottom: 10px; }
	&:after {
		content: "";
		background-color: $blue;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 213px;
		height: 5px;
		@include sm {
			width: 102px;
			left: 0; } }
	&_sm {
		&:after {
			width: 102px;
			@include sm {
				width: 51px; } } } }
